.card{
    margin:5px; 
    transition: transform 0.3s ease; 
    /* border: 2px solid red; */
    height: 15rem; 
    width: 10rem;
    
}



.card img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the image covers the whole card area */
}

.card {
    width: 3in;
    height: auto; /* Adjust the height to accommodate the image and title */
    margin: 5px;
    transition: transform 0.3s ease;
    position: relative;
    background-color: #000; /* Optional: Adds a background color */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Optional: Adds a shadow */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    box-sizing: border-box;

    min-width: 10rem;
    min-height: 15rem;
}

.card-img {
    width: 100%;
    height: auto; /* Maintain aspect ratio */
    object-fit: cover; /* Ensure image covers the full card area */
    border-radius: 5px; /* Optional: Adds rounded corners to the image */
}

.card-title {
    width: 100%;
    text-align: center;
    background: rgba(0, 0, 0, 0.7); /* Optional: Adds a semi-transparent background */
    color: white;
    padding: 5px;
    box-sizing: border-box;
    font-size: 0.8em; /* Adjust font size as needed */
    margin-top: 5px;
    border-radius: 5px; /* Optional: Adds rounded corners to the title */
}

.card:hover {
    transform: scale(1.05); /* Optional: Slightly increases size on hover */
    cursor: pointer;
}

 




.card:hover{
    margin:5px;
    transform: translateY(-5px);   
}

