.header{
    background-color: black;
    padding:1%;
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding-top:1%;
    padding-bottom:1%;
    box-sizing:border-box;
    margin-bottom: 5rem;
    /* width:100vw; */

    width: 100vw; /* Ensures the header spans the entire viewport width */
    height:4rem;
    position: fixed; /* Fixes the header to the top of the viewport */
    top: 0; /* Positions the header at the top */
    left: 0; /* Positions the header at the left edge */
    z-index: 1000; /* Ensures the header stays on top of other content */
    /* border: 2px solid white; */
}

.logo{
    margin-left: 0;
    padding-left: 0;
}

.topiccontainer{
    text-decoration: none;
    color:white;
    display:flex;
    align-items: center;
    justify-content: center;
    padding:0%;
    margin-right: 12rem;


    /* border:2px solid red; */
}

.topic{
    text-decoration: none;
    color:white;
    display:flex;
    align-items: center;
    justify-content: center;
    /* border:2px solid black; */
    padding:1%;
    width:10rem;
    margin-left: 0rem;
    margin-right:0rem;

    transition: color 0.3s ease, margin 0.3s ease, text-decoration 0.3s ease;
    
}

.topic:hover {
  color: white;
  font-weight: bold;
  margin-left: 1.3rem;
  margin-right: 1.3rem;
  /* Add underline effect */
  border-bottom: 2px solid white;
}


.searchbutton{
    color:white;
    margin-right: 2rem;
  }